body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "OpenSans-Light";
  src: url(/team_site_standart_1/static/media/OpenSans-Light.9ff12f69.ttf);
}
@font-face {
  font-family: "OpenSans-Regular";
  src: url(/team_site_standart_1/static/media/OpenSans-Regular.d7d5d458.ttf);
}
@font-face {
  font-family: "OpenSans-SemiBold";
  src: url(/team_site_standart_1/static/media/OpenSans-SemiBold.e1c83f94.ttf);
}
@font-face {
  font-family: "OpenSans-Bold";
  src: url(/team_site_standart_1/static/media/OpenSans-Bold.f5331cb6.ttf);
}


/*============================== Adaptive ==============================*/

@media (min-width: 0px) {
  .App {
    font-family: "OpenSans-Light";
    font-size: 14px;
  }

  .b-black {
    border: 1px solid black;
  }

  .b-red {
    border: 1px solid red;
  }

  .b-green {
    border: 1px solid green;
  }

  .b-yellow {
    border: 1px solid yellow;
  }

  .sidebar-main {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  .ci-position {
    width: auto;
    margin: 0;
    right: 5%;
    left: 80%;
    bottom: 30px;
    z-index: 2;
  }

  .cc-position {
    text-align: left;
    left: 5%;
    padding-bottom: 10px;
    bottom: 0;
    z-index: 2;
  }

  .bblock {
    height: 40%;
    width: 100%;
    background-image: -webkit-linear-gradient(rgba(255,0,0,0), rgb(0, 0, 0));
    background-image: linear-gradient(rgba(255,0,0,0), rgb(0, 0, 0));
    position: absolute;
    bottom: 0;
    opacity: 0.7;
    z-index: 1;
  }

  .header-adv .navbar-collapse {
    border-top: 1px solid rgba(42, 18, 151, 0.836);
    box-shadow: inset 0 1px 0 rgba(134, 130, 130, 0.877);
  }

  #navbarSupportedContent li > a {
    font-weight: 700;
  }

  /*.partners {
    position: relative;
    top: 80px;
  }*/

  .partner-logo {
    width: 40px;
  }

  .content {
    position: relative;
    top: 80px;
  }

  footer {
    position: relative;
    top: 80px;    
  }

  .f-info-logo {
    opacity: 0.5;
    -webkit-filter: grayscale(100%) contrast(150%);
            filter: grayscale(100%) contrast(150%);
  }

  .f-fima-info {
    font-size: 12px;
  }

  .f-other {
    font-size: 12px;
  }
}



@media (min-width: 750px) {
  /*.partners {
    top: 98px;
  }*/

  .partner-logo {
    width: 70px;
  }

  .content {
    top: 102px;
  }

  footer {
    top: 102px;    
  }
}
.header-adv h1 {
  font-family: "OpenSans-SemiBold";
  font-size: 16px;
  color: white;
  margin: 0;
}
